import React from "react";
import "./CodeExplain.css";
import SyntaxHighlighter from "react-syntax-highlighter";
import { a11yDark, dark } from "react-syntax-highlighter/dist/esm/styles/hljs";

const CodeExplain = () => {
  return (
    <div className="code-explain-section">
      <h1 id="explain">Code Explain</h1>
      <p>
        i vi v d ny chng mnh s dng th vin <b>remark-parse</b> 
        chuyn t MarkDown thnh MarkDown AST. Sau  s dng{" "}
        <b>remark-rehype</b>  chuyn t MarkDown AST sang HTML AST. Cui cng
        chng mnh s dng <b>rehype-string</b>  chuyn t HTML AST thnh HTML
      </p>
      <SyntaxHighlighter language="javascript" style={a11yDark}>
        {`import { unified } from "../Library/unified";
import markdown from "../Library/remark-parse";
import remarkRehype from "../Library/remark-rehype";
import rehypeStringify from "../Library/rehype-stringify";

        const converter = unified()  //To ra mt processor  thc hin cc cng vic pha di
          .use(markdown) //Chuyn t MarkDown sang MarkDown AST
          .use(remarkRehype)  //Chuyn t MarkDown AST sang HTML AST
          .use(rehypeStringify) //Chuyn t HTML AST sang HTML
          .process(e.target.value) //e.target.value chnh l input ca ngi dng
          .then((output) => {
            setOutPutValue(String(output)); //y l hm trong React  cp nht li UI
      })`}
      </SyntaxHighlighter>
      <br />
      <p>
        Ok! Vi nhng dng u tin th cc bn cng bit rng chng mnh s
        dng cch tip cn Interpreter th hai. Nhng dng code nh{" "}
        <b>unified()</b> cc bn khng cn quan tm u. Unified l mt th vin
        chuyn dng  tng tc vi AST. N chuyn dng bi v n c nhng tiu
        chun thng nht AST. Nu cc bn mun tham kho th vin Unified th
        ti mnh c t ng link  di mc ti liu tham kho y. Sau y
        chng ta s tip tc n phn phn tch source code th vin{" "}
        <b>remark-parse</b>
      </p>
      <br />
      <h3 id="remark-parse">Hm tr v ca th vin remark-parse</h3>
      <SyntaxHighlighter language="javascript" style={a11yDark}>
        {`export const fromMarkdown =

  function (value, encoding, options) {

    if (typeof encoding !== 'string') {
      options = encoding
      encoding = undefined
    }

    // Hm compiler s tr v con tr n hm compile vi nhng options c truyn vo
    const MarkDownCompile = compiler(options);

    // Phn tch c php chui u vo
    // Preprocess: Tokenize Chui
    // Postprocess: Subtokenize Chui
    let postprocessedString = postprocess(
      parse(options).document().write(preprocess()(value, encoding, true))
    );

    // Chuyn i t Chui  c phn tch c php thnh AST
    return MarkDownCompile(postprocessedString)
  }`}
      </SyntaxHighlighter>
      <br />
      <h3 id="compiler">Hm complier</h3>
      <SyntaxHighlighter language="javascript" style={a11yDark}>
        {`function compiler(options = {}) {

  // Tr v mt object config c dng nh JSON
  const config = configure(
    {
      // transform l nhng hm s tc ng ln AST, mc nh l rng
      transforms: [],
      // Nhng phn t c th cha k t EOL
      canContainEols: [
        'emphasis',
        'fragment',
        'heading',
        'paragraph',
        'strong'
      ],
      // Khi mnh push vo stack th ng vi phn t no nn dng hm no
      enter: {
        autolink: opener(link),
        autolinkProtocol: onenterdata,
        autolinkEmail: onenterdata,
        atxHeading: opener(heading),
        blockQuote: opener(blockQuote),
        characterEscape: onenterdata,
        characterReference: onenterdata,
        codeFenced: opener(codeFlow),
        codeFencedFenceInfo: buffer,
        codeFencedFenceMeta: buffer,
        codeIndented: opener(codeFlow, buffer),
        codeText: opener(codeText, buffer),
        codeTextData: onenterdata,
        data: onenterdata,
        codeFlowValue: onenterdata,
        definition: opener(definition),
        definitionDestinationString: buffer,
        definitionLabelString: buffer,
        definitionTitleString: buffer,
        emphasis: opener(emphasis),
        hardBreakEscape: opener(hardBreak),
        hardBreakTrailing: opener(hardBreak),
        htmlFlow: opener(html, buffer),
        htmlFlowData: onenterdata,
        htmlText: opener(html, buffer),
        htmlTextData: onenterdata,
        image: opener(image),
        label: buffer,
        link: opener(link),
        listItem: opener(listItem),
        listItemValue: onenterlistitemvalue,
        listOrdered: opener(list, onenterlistordered),
        listUnordered: opener(list),
        paragraph: opener(paragraph),
        reference: onenterreference,
        referenceString: buffer,
        resourceDestinationString: buffer,
        resourceTitleString: buffer,
        setextHeading: opener(heading),
        strong: opener(strong),
        thematicBreak: opener(thematicBreak)
      },
      // Khi mnh pop ra khi stack th ng vi phn t no chng ta nn dng hm no
      exit: {
        atxHeading: closer(),
        atxHeadingSequence: onexitatxheadingsequence,
        autolink: closer(),
        autolinkEmail: onexitautolinkemail,
        autolinkProtocol: onexitautolinkprotocol,
        blockQuote: closer(),
        characterEscapeValue: onexitdata,
        characterReferenceMarkerHexadecimal: onexitcharacterreferencemarker,
        characterReferenceMarkerNumeric: onexitcharacterreferencemarker,
        characterReferenceValue: onexitcharacterreferencevalue,
        codeFenced: closer(onexitcodefenced),
        codeFencedFence: onexitcodefencedfence,
        codeFencedFenceInfo: onexitcodefencedfenceinfo,
        codeFencedFenceMeta: onexitcodefencedfencemeta,
        codeFlowValue: onexitdata,
        codeIndented: closer(onexitcodeindented),
        codeText: closer(onexitcodetext),
        codeTextData: onexitdata,
        data: onexitdata,
        definition: closer(),
        definitionDestinationString: onexitdefinitiondestinationstring,
        definitionLabelString: onexitdefinitionlabelstring,
        definitionTitleString: onexitdefinitiontitlestring,
        emphasis: closer(),
        hardBreakEscape: closer(onexithardbreak),
        hardBreakTrailing: closer(onexithardbreak),
        htmlFlow: closer(onexithtmlflow),
        htmlFlowData: onexitdata,
        htmlText: closer(onexithtmltext),
        htmlTextData: onexitdata,
        image: closer(onexitimage),
        label: onexitlabel,
        labelText: onexitlabeltext,
        lineEnding: onexitlineending,
        link: closer(onexitlink),
        listItem: closer(),
        listOrdered: closer(),
        listUnordered: closer(),
        paragraph: closer(),
        referenceString: onexitreferencestring,
        resourceDestinationString: onexitresourcedestinationstring,
        resourceTitleString: onexitresourcetitlestring,
        resource: onexitresource,
        setextHeading: closer(onexitsetextheading),
        setextHeadingLineSequence: onexitsetextheadinglinesequence,
        setextHeadingText: onexitsetextheadingtext,
        strong: closer(),
        thematicBreak: closer()
      }
    },
    options.mdastExtensions || []
  )

  const data = {}
  return compile

  function compile(events) {

    // Khi to object tree vi node u tin l root c thuc tnh children rng
    let tree = {
      type: 'root',
      children: []
    }

    // Stack i din cho cy ca chng ta
    const stack = [tree]

    // Stack Token  lm vic vi Token
    const tokenStack = []

    // Stack List  lm vic vi phn t thuc mt List
    const listStack = []

    // Bin cha ton b cu hnh ca ton b hm compile
    const context = {
      stack,
      tokenStack,
      config,
      enter,
      exit,
      buffer,
      resume,
      setData,
      getData
    }

    // Chy ht ton b Input[] u vo
    let index = -1

    // X l trng hp cho phn t u vo l listOrdered hoc listUnordered
    while (++index < events.length) {
      if (
        events[index][1].type === 'listOrdered' ||
        events[index][1].type === 'listUnordered'
      ) {
        if (events[index][0] === 'enter') {
          listStack.push(index)
        } else {
          const tail = listStack.pop()
          index = prepareList(events, tail, index)
        }
      }
    }

    // X l cho cc trng hp cn li
    index = -1

    while (++index < events.length) {
      const handler = config[events[index][0]]

      if (own.call(handler, events[index][1].type)) {
        handler[events[index][1].type].call(
          Object.assign(
            {
              sliceSerialize: events[index][2].sliceSerialize
            },
            context
          ),
          events[index][1]
        )
      }
    }

    // Nu sau khi x l m vn cn Token trong Stack Token th thc hin x l exit
    if (tokenStack.length > 0) {
      const tail = tokenStack[tokenStack.length - 1]
      const handler = tail[1] || defaultOnError
      handler.call(context, undefined, tail[0])
    }

    // Gn thm thuc tnh v tr cho object tree cho ph hp vi tiu chun
    tree.position = {
      start: point(
        events.length > 0
          ? events[0][1].start
          : {
              line: 1,
              column: 1,
              offset: 0
            }
      ),
      end: point(
        events.length > 0
          ? events[events.length - 2][1].end
          : {
              line: 1,
              column: 1,
              offset: 0
            }
      )
    }

    // Nu chng ta c s dng thm plugs in no th chng ta s p dng ci  ln object tree
    index = -1

    while (++index < config.transforms.length) {
      tree = config.transforms[index](tree) || tree
    }

    // Tr v object tree
    return tree
  }`}
      </SyntaxHighlighter>
      <br />
      <p>
        By gi chng ta hy th vi paragraph. Code  trn ghi rng{" "}
        <b> paragraph: opener(paragraph) </b>. Theo nh code  trn th vi phn
        t paragraph th ta s gi n a ch hm <b>opener(paragraph)</b>. Ch
        ny cc bn phi ch  rng paragraph  bn tri l tn ca mt thuc
        tnh tr ti bt c th g tr v  bn phi. Cn paragraph bn phi l
        mt bin  c nh ngha  u  trong code ri.
      </p>
      <br />
      <h3 id="opener">Hm Opener</h3>
      <SyntaxHighlighter
        language="javascript"
        style={a11yDark}
      >{`      // Hm nhn 2 tham s. 
      // create l con tr n mt hm chuyn t Token thnh Node
      // and l mt tham s tr ti mt hm. Tham s ny khng bt buc phi truyn vo
      function opener(create, and) {
    return open
   
    // Hm open nhn vo Token
    function open(token) {
      // gi n hm enter vi 2 tham s truyn vo l create(token) v token
      enter.call(this, create(token), token)
      // nu nh c truyn vo tham s and. th gi lun hm and
      if (and) and.call(this, token)
    }
  }`}</SyntaxHighlighter>
      <p>
        Nh vy vi hm <b>opener(paragraph)</b> th <b>paragraph</b> chnh l
        tham s <i>create</i> ca hm <b>opener</b>. V qu tht{" "}
        <b>paragraph</b> bn v phi chnh l con tr ti mt hm chuyn t
        Token thnh Node.
      </p>
      <br />
      <h3 id="paragraph">Hm paragraph</h3>
      <SyntaxHighlighter language="javascript" style={a11yDark}>
        {`function paragraph() {
    return {
      type: 'paragraph',
      children: []
    }
  }`}
      </SyntaxHighlighter>
      <br />
      <p>
        Cui cng th hm open s gi n hm enter. Mnh ni cho cc bn lun
        l hm enter c chc nng l push node vo stack tree ca chng ta.
      </p>
      <br />
      <h3 id="enter">Hm enter</h3>
      <SyntaxHighlighter language="javascript" style={a11yDark}>
        {`function enter(node, token, errorHandler) {
    const parent = this.stack[this.stack.length - 1]
    parent.children.push(node)
    this.stack.push(node)
    this.tokenStack.push([token, errorHandler]) 
        node.position = {
      start: point(token.start)
    }
    return node
  }`}
      </SyntaxHighlighter>
      <br />
      <p>
        Cui cng th vi object tree ca chng ta th cc bn c th d dng
        chuyn t mt JSON Like sang ngn ng ch ca cc bn. V d nh vi
        cc phn t no c <b>type</b> l <i>Heading</i> th cc bn s thm vo
        chui HTML {'"<h"'}. Sau  vi <b>depth = 1</b> th cc bn thm vo{" "}
        {'"1>"'}
      </p>
    </div>
  );
};

export default CodeExplain;
